import {ActionTree, GetterTree, Module, MutationTree} from 'vuex';
import {StateInterface} from '@/store';
import {CardItem} from "@/types/CardItem";
// @ts-ignore
import {supabase} from '@/lib/supabaseClient'
import {SubItem} from "@/types/SubItem";
import {SubItemLesson} from "@/types/SubItemLesson";
import {LessonPage} from "@/types/LessonPage";

export interface ExampleStateInterface {
    test1: string,
    modulesSub: SubItem[],
    lessonsSub: SubItem[],
    lessonPage: LessonPage,
    tmpModule: SubItem,
    tmpLesson: SubItemLesson,
    flag: boolean,
    showCreateLesson: boolean,
    showLessonsList: boolean,
    showLesson: boolean,
}

function state(): ExampleStateInterface {
    return {
        test1: 'Test',
        modulesSub: [],
        lessonsSub: [],
        tmpModule: {id: -1, title: '', description: '', created_at: ''},
        tmpLesson: {id: -1, module_id: -1, title: '', description: '', created_at: ''},
        lessonPage: {id: -1, lesson_id: -1, page: '', created_at: ''},
        flag: false,
        showCreateLesson: false,
        showLessonsList: false,
        showLesson: false,
    }
}

const mutations: MutationTree<ExampleStateInterface> = {

    setModules(state, payload) {
        state.modulesSub = payload
    },
    addSubModule(state, payload: SubItem) {
        console.log(payload)

        state.modulesSub.push(payload)
    },
    setLessons(state, payload) {
        state.lessonsSub = payload
    },
}
const actions: ActionTree<ExampleStateInterface, StateInterface> = {
    async selectModules({state, commit}) {
        state.modulesSub = []
        await supabase.from('modules')
            .select()
            .order('id', {ascending: true})
            .then(({data}: { data: SubItem[] }) => {
                console.log(data)
                commit("setModules", data)
            })
    },
    async selectModule({state, dispatch}, {id}) {
        state.lessonsSub = []
        state.flag = false
        await supabase.from('modules')
            .select('id,title,description')
            .eq('id', id)
            .then(({data}: { data: SubItem[] }) => {
                console.log('selectModule:')
                console.log(data[0])
                state.tmpModule = data[0]
                dispatch('selectLessons', {id: data[0].id})
                state.flag = true
            })
    },
    async createModule({state, commit}, {item}: { item: SubItem }) {
        await supabase.from('modules')
            .insert({title: item.title, description: item.description}).select()
            .then(({data}: { data: SubItem[] }) => {
                console.log(data)
                // commit('addSubModule',data[0])
                state.tmpModule = data[0]
            })
    },
    async updateModule({state, commit}, {item}: { item: SubItem }) {
        state.flag = false
        await supabase.from('modules')
            .update({title: item.title, description: item.description})
            .eq('id', item.id)
            .then((resp: { resp: any }) => {
                console.log(resp)
                state.flag = true
            })
    },
    async createLesson({state, dispatch}, {item}: { item: SubItemLesson }) {
        await supabase.from('lessons')
            .insert({title: item.title, description: item.description, module_id: item.module_id,})
            .select()
            .then((resp: { resp: any }) => {
                console.log(resp)
                state.showCreateLesson = false
                dispatch('selectLessons', {id: state.tmpModule.id})
            })
    },
    async updateLesson({state, commit}, {item}: { item: SubItemLesson }) {
        state.showLesson = false
        await supabase.from('lessons')
            .update({title: item.title, description: item.description})
            .eq('id', item.id)
            .then((resp: { resp: any }) => {
                console.log(resp)
                state.showLesson = true
            })
    },
    async selectLesson({state, dispatch}, {id}) {
        state.showLesson = false
        await supabase.from('lessons')
            .select()
            .eq('id', id)
            .then(({data}: { data: SubItemLesson[] }) => {
                console.log('selectLesson:')
                console.log(data[0])
                state.tmpLesson = data[0]
                dispatch('selectLessonPage', {id: data[0].id})
                state.showLesson = true
            })
    },
    async selectLessons({state, commit}, {id}) {
        state.showLessonsList = false
        console.log('selectLessons:')
        console.log(id)
        await supabase.from('lessons')
            .select()
            .eq('module_id', id)
            .order('id', {ascending: true})
            .then(({data}: { data: SubItemLesson[] }) => {
                console.log(data)
                commit('setLessons', data)
                state.showLessonsList = true
            })
    },
    async selectLessonPage({state, dispatch}, {id}) {
        await supabase.from('lesson_pages')
            .select()
            .eq('lesson_id', id)
            .then(({data}: { data: LessonPage[] }) => {
                console.log('selectLessonPage:')
                console.log(data[0])
                if (data.length > 0)
                    state.lessonPage = data[0]
            })
    },
    async createLessonPage({state, dispatch}, {item}: { item: LessonPage }) {
        await supabase.from('lesson_pages')
            .insert({page: item.page, lesson_id: item.lesson_id,})
            .select()
            .then(({data}: { data: LessonPage[] }) => {
                console.log(data)
                state.lessonPage=data[0]
            })
    },
    async updateLessonPage({state, commit}, {item}: { item: LessonPage }) {
        await supabase.from('lesson_pages')
            .update({page: item.page})
            .eq('id', item.id)
            .then((resp: { resp: any }) => {
                console.log(resp)
            })
    },

}
const getters: GetterTree<ExampleStateInterface, StateInterface> = {
    someGetter(state) {
        return state.test1;
    }, getModules(state) {
        return JSON.parse(JSON.stringify(state.modulesSub))
    },
    getModule: (state) => (val: number) => {
        console.log('getModule:')
        return JSON.parse(JSON.stringify(state.modulesSub.find(item => item.id == val)))
    },
    getSubModule: (state) => (val: number) => {
        return JSON.parse(JSON.stringify(state.modulesSub.find(item => item.id == val)))
    },
}

const lessonModule: Module<ExampleStateInterface, StateInterface> = {
    namespaced: true,
    actions,
    getters,
    mutations,
    state
}


export default lessonModule;